async function processData(rows) {
    const columns = await showMe(rows);
    let [serviceNumbers, serviceUsages] = await buildServiceUsages(rows,columns);
    console.log(serviceNumbers);
    console.log(serviceUsages);
    let headerColumns = columns.headerColumns;
    const accountNo = rows[1][headerColumns.get("Account Number".toUpperCase())];
    const cidn = rows[1][headerColumns.get("CIDN".toUpperCase())];
    //await bulkPurgeAll(cidn,accountNo);
    bulkPurgeAll(cidn,accountNo).then((res) => {
        console.log("I think purgeAll is completed now calling bulk inserts");
        bulkInsertSn(cidn,accountNo,serviceNumbers).then((res)=>{
            countAllSn();
        })
        bulkInsertSnUsage(cidn,accountNo,serviceUsages).then((res) => {
            store.dispatch('setProgress',false);
        })
    });
    //updateAccountSummaryChart();
    //await findAccountSummary();
    return Promise.resolve([serviceNumbers, serviceUsages]);
}


async function showMe(rows) {

    console.log('showme injesting  ' + rows.length + " progress " + store.getters.isShowProgress + " row 0 is ");
    let callMonth = [];
    const headerColumns = new Map();
    const headerRow = rows[0];
    let i = 0; const iMax = headerRow.length;
    for(; i < iMax; i++) {
        let matchResult =  headerRow[i].toUpperCase().match(mtPattern);
        headerColumns.set(headerRow[i].toUpperCase(),i);
        if (matchResult) {
            //console.log("column header " + headerRow[i] + " match " + matchResult);
            let monthLabel = matchResult[2];
            let year = matchResult[3];
            let category = matchResult[5]; //eg DOMESTIC DATA
            let categoryGroup = matchResult[5].split(' ').join('-');
            let categoryType = matchResult[6]; //eg CALLS | USAGES | MINUTES OF USER USAGE |
            let colIndex = i; //index starts at 0
            let id = matchResult[1].split(' ').join('-') + '-' +i;
            callMonth.push({_id: id,colIndex:colIndex,year:year,monthLabel:monthLabel,category:category, categoryGroup: categoryGroup, categoryType:categoryType});
        }
    }
    //save it...
    //store.dispatch('saveCallMonth',callMonth);
    console.log("showme injested " + callMonth.length + " call month");
    return Promise.resolve({callMonth: callMonth, headerColumns: headerColumns});
}

/**
 * create object with serviceNumber, accountNumber, category, call, minutes, usage, excess usage...
 * create object with serviceNumber, accountNumber, category, count, duration minutes, usage - mb, excess usage - mb, sms
 * @param rows
 * @param callMonth
 * @returns {Promise<[]>}
 */
async function buildServiceUsages(rows, columns) {
    let serviceUsages = [];
    let serviceNumbers = [];
    let callMonth = columns.callMonth;
    let headerColumns = columns.headerColumns;
    let i = 1; const iMax = rows.length; //row 0 is the header hence setting i to 1
    const groupedCategoryByYearMonth = groupBy(callMonth, callCat => callCat.categoryGroup+"-"+callCat.year+"-"+callCat.monthLabel);
    console.log(groupedCategoryByYearMonth);
    for(; i < iMax; i++) {
        let serviceLine = rows[i];
        let accountNo = serviceLine[headerColumns.get("Account Number".toUpperCase())];
        let sn = serviceLine[headerColumns.get("Mobile Number".toUpperCase())];
        //go build the count, duration, usage etc
        groupedCategoryByYearMonth.forEach((value,key,map) => { //eg DOMESTIC-DATA-2018-AUG
            let call = 0;
            let duration = 0; //minutes
            let usage = 0; //mb
            let excess = 0;
            let sms = 0;
            value.forEach(callMonth =>{ //eg AUG-2018-DOMESTIC-DATA-CALLS-94
                let categoryType = callMonth.categoryType;
                let colIndex = callMonth.colIndex;
                let colValue = serviceLine[colIndex];
                if (categoryType == 'CALLS') {
                    call = colValue;
                }
                if (categoryType == 'MINUTES OF USE') {
                    duration = colValue;
                }
                if (categoryType == 'USAGE (MB)') {
                    usage = colValue;
                }
                if (categoryType == 'EXCESS USAGE ($)') {
                    excess = colValue;
                }
                if (categoryType == 'SMS') {
                    sms = colValue;
                }
                //use the case of usage, duration, call, excess etc
                //console.log(callMonth);
            })
            const groupMatch = key.match(groupPattern);
            let category = groupMatch[2].split('-').join(' '); //DOMESTIC-DATA to DOMESTIC DATA
            let categoryYear = groupMatch[3]; //eg 2018
            let monthLabel = groupMatch[5]; //eg FEB
            //here we create a new serviceline...
            let serviceUsage = {
                _id: sn+"-"+category+"-"+categoryYear+monthHash[monthLabel],
                sn: sn,
                acct: accountNo,
                lastBillDate: serviceLine[headerColumns.get("Last Bill Date".toUpperCase())],
                category: category,
                year: categoryYear,
                monthLabel: monthLabel,
                yearMonth: categoryYear + monthHash[monthLabel],
                call: call,
                duration: duration,
                usage: usage,
                excess: excess,
                sms: sms
            }
            serviceUsages.push(serviceUsage);
        });
        //create the service number...
        let serviceNumber = {
            _id: sn,
            sn: sn,
            acct: accountNo,
            cidn: serviceLine[headerColumns.get("CIDN".toUpperCase())],
            userName: serviceLine[headerColumns.get("USER NAME".toUpperCase())],
            sim: serviceLine[headerColumns.get("SIM Number".toUpperCase())],
            simSerial: serviceLine[headerColumns.get("SIM Serial Number".toUpperCase())],
            simNetwork : serviceLine[headerColumns.get("SIM Network".toUpperCase())],
            snActivationDate: serviceLine[headerColumns.get("Service Activation Date".toUpperCase())],
            snEndDate: serviceLine[headerColumns.get("Service End Date".toUpperCase())],
            snStatus: serviceLine[headerColumns.get("Service Status".toUpperCase())],
            flexiPlan: serviceLine[headerColumns.get("Flexiplan".toUpperCase())],
            flexiPlanName: serviceLine[headerColumns.get("Flexiplan Name".toUpperCase())],
            flexiStart: serviceLine[headerColumns.get("Flexiplan Start Date".toUpperCase())],
            flexiEnd: serviceLine[headerColumns.get("Flexiplan End Date".toUpperCase())],
            imei: serviceLine[headerColumns.get("IMEI".toUpperCase())],
            make: serviceLine[headerColumns.get("Make".toUpperCase())],
            model: serviceLine[headerColumns.get("Model".toUpperCase())],
            deviceName: serviceLine[headerColumns.get("Device Name".toUpperCase())],
            deviceType: serviceLine[headerColumns.get("Device Type".toUpperCase())],
            deviceCapability: serviceLine[headerColumns.get("Device Capability".toUpperCase())],
        }
        serviceNumbers.push(serviceNumber);
    } //end of for loop
    return Promise.resolve([serviceNumbers, serviceUsages]);
}

function groupBy(list, keyGetter) {
    const map = new Map();
    list.forEach((item) => {
        const key = keyGetter(item);
        const collection = map.get(key);
        if (!collection) {
            map.set(key, [item]);
        } else {
            collection.push(item);
        }
    });
    return map;
}

