import  Vue from 'vue';
import Vuex from 'vuex';
import PouchDb from 'pouchdb';
import {updateAccountSummaryChart,bulkPurgeAll} from '@/api/PouchDao'


const callMonthDb = new PouchDb('callMonth');

Vue.use(Vuex);


export default new Vuex.Store({
    state: {
        isShowProgress : false,
        rowsRead: 0,
        containsData: false,
        accountSummaryChart: {
            series: [],
            labels: []
        }
    },
    getters: {
        // Here we will create a getter
        isShowProgress: state =>  state.isShowProgress
        ,
        rowsRead: state =>  state.rowsRead
        ,
        accountSummaryChart: state =>  state.accountSummaryChart
        ,
        containsData: state =>  state.containsData

    },

    mutations: {
        // Here we will create Jenny
        // Jenny
        commitProgress(state, progress) {
            console.log("commit progress is "+ progress);
            state.isShowProgress = progress
        },
        commitContainsData(state, containsData) {
            state.containsData = containsData;
        },
        commitRowsRead(state, count) {
            state.rowsRead = count
        },
        commitAccountSummaryChart(state,data) {
          state.accountSummaryChart = data;
        },

        },
    actions: {
        updateContainsData(context,data) {
            console.log("updateContainsData returns " + data.count);
            context.commit('commitContainsData',data.count>0);
            if (data.count>0) {
                context.commit('commitRowsRead',data.count);
            } else {
                context.commit('commitRowsRead',0);
            }
        },
        updateAccountSummaryChart(context, data) {
            context.commit('commitAccountSummaryChart',data);
        },
        setProgress(context, progress) {
            console.log("action progress is "+ progress);
            context.commit('commitProgress',progress);
        },
        resetCallMonthDb() {
          console.log('reset callMonthDb');
          callMonthDb.destroy().then(function () {
              // database destroyed
              console.log('callMonthDb destroyed');
          }).catch(function (err){
              // error occurred
              console.log(err);

          });
        },
        compactCallMonth() {
            console.log('compacting callMonthDb');
            bulkPurgeAll();
            return callMonthDb.compact().then(function (info) {
                // compaction complete
                console.log(info);
            }).catch(function (err) {
                // handle errors
                console.log(err);
            });
        },
        saveCallMonth(context,callMonthArray) {
            //delete all
            console.log("saving callMonth size is " + callMonthArray.length);
            callMonthDb.allDocs({include_docs: true}).then(allDocs => {
                return allDocs.rows.map(row => {
                    return {_id: row.id, _rev: row.doc._rev, _deleted: true};
                });
            }).then(deleteDocs => {
                callMonthDb.bulkDocs(deleteDocs).then(() => {
                    //console.log(res);
                    //successfully deleted now insert the new list of callMonth
                    callMonthDb.bulkDocs(callMonthArray).then(() => {
                        //console.log(res);
                        console.log("Call months "+callMonthArray.length+" inserted OK");
                        callMonthDb.allDocs().then(entries => console.log(entries.rows.length));
                    }).catch((err) => {
                        console.error(err);
                    });
                }).catch((err) => {
                    console.error(err);
                });
            });
        }

    }
});