<template>
    <v-dialog
            v-model="dialog"
            hide-overlay
            persistent
            width="300"
    >
        <v-card
                color="primary"
                dark
        >
            <v-card-text>
                Please stand by {{title}}
                <v-progress-linear
                        indeterminate
                        color="white"
                        class="mb-0"
                ></v-progress-linear>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>

    import { mapState } from 'vuex';

    export default {
        name: "ProgressDialog",
        data: () => ({
            title: "Blitz loading...",
            dialog: false //was true mean dialog will appear
        }),
        computed: mapState(['isShowProgress']),
        mounted() {
            this.$store.watch(
                (state, getters) => getters.isShowProgress,
                (newValue, oldValue) => {
                    console.log(`mounted!! watching isShowProgress from ${oldValue} to ${newValue}`);
                    if (newValue) {
                        this.dialog = true;
                    } else {
                        this.dialog = false;
                    }
                });

        },
        methods: {

        }

    }
</script>

