<template >
    <v-container v-if="!containsData">
        <v-layout
                text-center
                wrap>
            <v-flex mb-4>
                <!-- need to look at adding :rules -->
                <v-file-input  :rules="rules"  @change="uploadFile" display-size label="File input" placeholder="Add your blitz file here" accept="xlsx,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"></v-file-input>
                {{rowsRead}} rows
            </v-flex>
        </v-layout>
    </v-container>
    <v-container v-else-if="containsData">
            <account-pie-chart ></account-pie-chart>
    </v-container>
</template>

<script>
    //import readXlsxFile from 'read-excel-file'; //need to remove this from package.json
    import XLSX from 'xlsx';
    import {processData} from "@/api/Preprocessor";
    //import {buildServiceUsages} from "@/api/Preprocessor";
    import accountPieChart from "@/components/AccountPieChart";

    export default {
        name: "Dashboard",
        components: {
          accountPieChart,
        },
        data () {
         return {
             showFilUpload: false,
            rules: [
                value => !value || value.size < 2000000 || 'File size should be less than 2 MB!',
            ],
        }
        },
        watch : {
            // whenever showprogress changes, this function will run
            showProgress :  function(){
                console.log('showprogress watch triggered value');
                //if (!newValue) return;
                //this.showprogress = newValue;
                //setTimeout(() => (this.showprogress = false), 4000);
            }
        },
        computed : {
            showProgress() {
                return this.$store.getters.showProgress;
            },
            containsData() {
                return this.$store.getters.containsData;
            },
            rowsRead() {
                return this.$store.getters.rowsRead;
            },
        },
        methods : {
            uploadFile: function (e) {
                const bfile = e;
                this.$store.dispatch('setProgress',true); //dispatch to the setProgress action in the store
                console.log("uploadFile clicked file name is " + bfile.name + ' show progress is ' + this.$store.getters.isShowProgress);
                let fileReader = new FileReader();
                fileReader.onload = function (e) {
                    //let filename = bfile.name;
                    // call 'xlsx' to read the file
                    let wb = XLSX.read(e.target.result, {type: 'buffer', cellDates:true, cellStyles:true});
                    const ws = wb.Sheets[wb.SheetNames[0]];
                    const rows = XLSX.utils.sheet_to_json(ws, {header:1, dateNF:'DD/MM/YYYY'});
                    console.log("completed reading xls " + bfile.name);
                    //console.log(data);
                    processData(rows);
                };
                fileReader.readAsArrayBuffer(bfile);
                /*
                readXlsxFile(bfile).then((rows) => {
                    console.log('import ' + rows.length + " rows from " + bfile.name );
                    this.injestRows(rows,this.$store);
                });
                */
            }
        },
        mounted() {
            //this.$store.dispatch('setProgress',true); //dispatch to the setProgress action in the store
        }
    }
</script>

